import {resetPwdStep2} from '../../api/api.js'
export default {
	data() {
		return {
			confirmPwd: '',
			key: '',
			mobile: '',
			password: ''
		};
	},
	onLoad(options) {
		let data = JSON.parse(options.data)
		this.mobile = data.mobile
		this.key = data.key
	},
	methods:{
		submit() {
			resetPwdStep2({
				confirmPwd: this.confirmPwd,
				key: this.key,
				mobile: this.mobile,
				password: this.password
			}).then(res => {
				uni.showToast({
					title: res.message
				})
			})
		}
	}
}
